import http from './http'

export const RegisterApi = (model) => http.post('/web/register', model)
export const LoginApi = (model) => http.post('/web/login', model)
export const PersonApi = () => http.get('/web/user/center')
export const WebHomeApi = () => http.get('/web/Index')
export const WebShopApi = () => http.get('/web/shop')

export const WebAboutApi = () => http.get('/web/info/about')
export const WebPostApi = () => http.get('/web/info/post')
export const WebPostReadApi = (id) => http.delete('/web/info/post/' + id)
