import ShopCard from 'component/web/ShopCard';
import { useEffect, useState } from 'react';
import { MdOutlineWhereToVote } from 'react-icons/md';
import { GiGriffinShield } from 'react-icons/gi';
import { BsFillFileEarmarkPostFill, BsCloudDownload } from 'react-icons/bs';
import { useTranslation } from 'react-i18next';
import 'react-responsive-carousel/lib/styles/carousel.min.css'; // requires a loader
import { Carousel } from 'react-responsive-carousel';
import { useNavigate } from 'react-router-dom';
import {WebHomeApi} from '../../utils/webApi'

const Home = () => {
  const { t } = useTranslation();
	const navigate = useNavigate()
	const [swiperData, setSwiperData] = useState([])
	const [productData, setProductData] = useState([])
  useEffect(() => {
    localStorage.setItem('navBar', '');
		WebHomeApi().then((res) => {
			setSwiperData(res.data.rotates.map((item) => ({imgUrl: process.env.REACT_APP_SERVER + item.imgUrl})))
			setProductData(res.data.product.map((item) => ({...item, imageUrl: process.env.REACT_APP_SERVER + item.imageUrl, tags: ['共享充电','共享收益']})))
		})
  },[]);
  return (
    <div className="flex flex-col ">
      <Carousel
        autoPlay={true}
        infiniteLoop={true}
        showThumbs={false}
        showArrows={false}
        showStatus={false}
      >
				{
				swiperData.map((item, index) => <img key={index} src={item.imgUrl} alt="" className='h-48 w-3/4 ' />)
				}
 
      </Carousel>

      <div className="flex justify-around items-center h-24">
        <div className="flex flex-col items-center gap-2">
          <MdOutlineWhereToVote className="text-4xl text-teal-300" />
          <p className="text-sm">{t('nearby')}</p>
        </div>

        <div className="flex flex-col items-center gap-2" onClick={() => navigate('/depot')}>
          <GiGriffinShield className="text-4xl text-teal-300" />
          <p className="text-sm">{t('depot')}</p>
        </div>

        <div className="flex flex-col items-center gap-2" onClick={() => navigate('/post')}>
          <BsFillFileEarmarkPostFill className="text-4xl text-teal-300" />
          <p className="text-sm">{t('post')}</p>
        </div>

        <div className="flex flex-col items-center gap-2" onClick={() => navigate('/download')}>
          <BsCloudDownload className="text-4xl text-teal-300" />
          <p className="text-sm">{t('download')}</p>
        </div>
      </div>

      <div className=" flex flex-col ">
        {productData.map((item) => (
          <ShopCard key={item._id} item={item} />
        ))}
      </div>
    </div>
  );
};

export default Home;
